import { useState } from "react";
import Head from "next/head";
import styled from "styled-components";
import hash from "object-hash";
import { articles, Article as ArticleType } from "../data/articles";
import { NavBar, Article, Footer } from "../components";

export function findWord(word, str) {
  return str.split(" ").some(function(w) {
    return w.startsWith(word);
  });
}

export default () => {
  const [value, setValue] = useState("");
  const articleFilter = ({ headline, author, topics, body }) =>
    (topics && topics.some(topic => topic.toLowerCase().startsWith(value))) ||
    (author && findWord(value, author.toLowerCase())) ||
    (headline && findWord(value, headline.toLowerCase())) ||
    (body && findWord(value, body.toLowerCase()));
  return (
    <Centered>
      <Head>
        <title>WebAssembly</title>
      </Head>
      <NavBar onChange={value => setValue(value.toLowerCase())} value={value} />
      <Articles articles={articles.filter(articleFilter)} />
    </Centered>
  );
};

export const Centered = styled.div``;

export const Articles = ({ articles }: { articles: ArticleType[] }) => (
  <ContentArea>
    <ArticleColumns>
      {articles.map(article => (
        <Article key={hash(article)} data={article} />
      ))}
    </ArticleColumns>
    <Footer />
  </ContentArea>
);

export const ArticleColumns = styled.div`
  display: grid;
  width: 100%;
  grid-gap: 10px;
  grid-row-gap: 30px;
  grid-column-gap: 50px;
  grid-template-columns: repeat(auto-fit, minmax(325px, 1fr));
`;

export const ContentArea = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 92%;
  padding-left: 4%;
  padding-right: 4%;
  @keyframes enter {
    0% {
      opacity: 0;
      transform: translate3d(0, 100px, 0);
    }
    50% {
      opacity: 0;
      transform: translate3d(0, 100px, 0);
    }
    100% {
      opacity: 1;
      transform: translate3d(0, 0, 0);
    }
  }

  animation: enter 0.5s;
`;



